.ng-dropdownsmtree-css{
    width:100%
}

.ng-dropdownsmtree-css .dropdown-toggle > .glyphicon{
    margin-top:3px;
}

.ng-dropdownsmtree-css .has-data {
    color: #ff6a00 !important;
    -webkit-transition: color 0.2s ease-in;
    -moz-transition: color 0.2s ease-in;
    -o-transition: color 0.2s ease-in;
    -ms-transition: color 0.2s ease-in;
    transition: color 0.2s ease-in;
}

.ng-dropdownsmtree-css .disabled {
    cursor: not-allowed;
    filter: alpha(opacity=65);
    -webkit-box-shadow: none;
    box-shadow: none;
    opacity: .65;
}

.ng-dropdownsmtree-inner-dropdown-menu {
    width: 100%;
    font-size: 12px;
    max-height: 300px;
    overflow-y: auto;
}


    .ng-dropdownsmtree-inner-dropdown-menu .content {
        display: none;
        overflow-x: hidden;
        max-height: 160px;
        overflow-y: auto;
        margin-left: 15px;
    }


    .ng-dropdownsmtree-inner-dropdown-menu .content > ul {
        padding-left: 15px;
    }


    .ng-dropdownsmtree-inner-dropdown-menu > li {
        padding: 3px 15px;
    }


        .ng-dropdownsmtree-inner-dropdown-menu > li > span {
            color: #828282;
            cursor: pointer;
        }


            .ng-dropdownsmtree-inner-dropdown-menu > li > span > .glyphicon {
                margin-right: 5px;
                font-size: 9px;
            }


.bordered-control {
    border: 1px solid #CCC;
    text-align: left;   
}